  min-height: 100vh;
  padding-top: 80px;
  background-color: var(--primary-color);
  color: var(--text-color);
}

.service-hero {
  padding: 8rem 10% 6rem;
  text-align: center;
  position: relative;
  overflow: hidden;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.service-hero h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  font-weight: 700;
  letter-spacing: 2px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 1;
}

.service-hero p {
  font-size: 1.5rem;
  max-width: 800px;
  margin: 0 auto;
  color: #cccccc;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  position: relative;
  z-index: 1;
}

.service-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 5%;
}

.service-content section {
  margin-bottom: 4rem;
}

.service-overview {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 4rem;
}

.service-overview h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: var(--accent-color);
}

.service-overview p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #cccccc;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  margin-top: 2rem;
}

.features-grid .feature-card:last-child:nth-child(odd) {
  grid-column: 1 / -1;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.feature-card {
  background: rgba(255, 255, 255, 0.05);
  padding: 2rem;
  border-radius: 10px;
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
}

.feature-card h3 {
  color: var(--accent-color);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.feature-card p {
  color: #cccccc;
  line-height: 1.6;
}

.service-benefits {
  background: rgba(255, 255, 255, 0.05);
  padding: 3rem;
  border-radius: 10px;
}

.service-benefits h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
  color: var(--accent-color);
  text-align: center;
}

.service-benefits ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
  list-style: none;
  padding: 0;
}

.service-benefits li {
  color: #cccccc;
  font-size: 1.1rem;
  padding-left: 1.5rem;
  position: relative;
}

.service-benefits li::before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--accent-color);
}

.service-cta {
  text-align: center;
  padding: 4rem 0;
  background: linear-gradient(rgba(87, 173, 207, 0.1), rgba(87, 173, 207, 0.05));
  border-radius: 10px;
}

.service-cta h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--accent-color);
}

.service-cta p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: #cccccc;
}

.service-cta .cta-button {
  display: inline-block;
  padding: 1rem 3rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-color);
  background: var(--accent-color);
  border: none;
  border-radius: 5px;
  -webkit-text-decoration: none;
  text-decoration: none;
  transition: all 0.3s ease;
}

.service-cta .cta-button:hover {
  transform: translateY(-2px);
  background: #68B8D6;
}

.service-process {
  background: rgba(255, 255, 255, 0.05);
  padding: 3rem;
  border-radius: 10px;
  margin-bottom: 4rem;
}

.service-process h2 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
  color: var(--accent-color);
  text-align: center;
}

.service-process p {
  color: #cccccc;
  font-size: 1.1rem;
  margin-bottom: 2rem;
  text-align: center;
}

.service-process ol {
  list-style-position: inside;
  padding: 0;
  counter-reset: process-counter;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.service-process li {
  color: #cccccc;
  font-size: 1.1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  counter-increment: process-counter;
  display: flex;
  align-items: center;
}

.service-process li::before {
  content: counter(process-counter);
  min-width: 30px;
  height: 30px;
  background: var(--accent-color);
  color: var(--primary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
  font-weight: bold;
}

/* Breadcrumb Navigation */
.breadcrumb {
  padding: 1rem 5%;
  background: rgba(255, 255, 255, 0.05);
  margin-bottom: 2rem;
}

.breadcrumb ol {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}

.breadcrumb li {
  display: flex;
  align-items: center;
  color: #cccccc;
}

.breadcrumb li:not(:last-child)::after {
  content: "/";
  margin: 0 0.5rem;
  color: var(--accent-color);
}

.breadcrumb a {
  color: var(--accent-color);
  -webkit-text-decoration: none;
  text-decoration: none;
  transition: color 0.3s ease;
}

.breadcrumb a:hover {
  color: #68B8D6;
}

/* FAQ Section */
.service-faq {
  background: rgba(255, 255, 255, 0.05);
  padding: 3rem;
  border-radius: 10px;
  margin-bottom: 4rem;
}

.service-faq h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
  color: var(--accent-color);
  text-align: center;
}

.faq-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.faq-item {
  background: rgba(255, 255, 255, 0.03);
  padding: 2rem;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.faq-item:hover {
  transform: translateY(-5px);
}

.faq-item h3 {
  color: var(--accent-color);
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.faq-item p {
  color: #cccccc;
  line-height: 1.6;
}

@media (max-width: 768px) {
  .service-hero {
    padding: 6rem 5% 4rem;
    min-height: 300px;
  }

  .service-hero h1 {
    font-size: 2.5rem;
  }

  .service-hero p {
    font-size: 1.2rem;
  }

  .service-content {
    padding: 2rem 5%;
  }

  .service-overview h2 {
    font-size: 2rem;
  }

  .feature-card {
    padding: 1.5rem;
  }

  .service-benefits {
    padding: 2rem;
  }

  .service-cta {
    padding: 3rem 1rem;
  }

  .service-cta h2 {
    font-size: 2rem;
  }

  .service-process {
    padding: 2rem;
  }

  .service-process ol {
    grid-template-columns: 1fr;
  }

  .service-process li {
    font-size: 1rem;
  }

  .breadcrumb {
    padding: 0.5rem 5%;
  }

  .breadcrumb ol {
    font-size: 0.9rem;
  }

  .service-faq {
    padding: 2rem;
  }

  .faq-grid {
    grid-template-columns: 1fr;
  }

  .faq-item {
    padding: 1.5rem;
  }
